$color-back: #023047;
$color-orange: #FB8500;
$color-white: #FFFFFF;
$color-black: #000000;

.icon{
  width: 18px;
  height: 18px;
  display: inline-block;
  vertical-align: sub;

  mask-size: 100%;
  mask-repeat: no-repeat;
  -webkit-mask-size: 100%;
  -webkit-mask-repeat: no-repeat;

  background-color: #A5A5A5;

  &-search{
    mask-image: url("/icons/search.svg");
    -webkit-mask-image: url("/icons/search.svg");
  }
  &-notification{
    mask-image: url("/icons/notification.svg");
    -webkit-mask-image: url("/icons/notification.svg");
  }
  &-users{
    mask-image: url("/icons/users.svg");
    -webkit-mask-image: url("/icons/users.svg");
  }
  &-stack{
    mask-image: url("/icons/stack.svg");
    -webkit-mask-image: url("/icons/stack.svg");
  }
}

body {
  background-color: #F9F9F9 !important;
  color: $color-black;
}

.min-title {

  font-weight: 600;
  font-size: 36px;
  color: $color-black;

}

a:hover {
  text-decoration: none !important;
}

.top-navbar {
  background-color: $color-back;
  height: 100px;
  width: 100%;

  .container {

    height: 100%;

    .row {
      justify-content: space-between;
      height: 100%;

      .top-navbar-logo {

        height: 100%;
        display: flex;
        align-items: center;

        h1 {
          font-weight: 600;
          font-size: 18px;
          line-height: 26px;
          text-transform: uppercase;
          color: $color-white;
          margin: 0;
          padding: 0;
          margin-left: 20px;

        }
      }

      .top-search {


        height: 100%;
        display: flex;
        align-items: center;
        width: 261px;

        position: relative;

        span {
          position: absolute;
          left: 21.5px;
          background-color: #FFFFFF;
        }

        input {
          &:focus {
            box-shadow: none;
          }
          outline: none;
          height: 50px;
          background: #345164;
          border-radius: 50px;
          width: 100%;
          border: none;
          color: #FFFFFF;
          padding-left: 50px;
          font-size: 14px;
          &::placeholder{
            color: #FFFFFF;
            opacity: 0.4;
          }
        }
      }
    }

  }
}

