.home-partners {
  height: 600px;
  padding-top: 100px;
  padding-left: 100px;
  padding-right: 100px;

  overflow: hidden;


  h2 {
    margin-bottom: 58px;
  }

  div {
    text-align: center !important;

  }

  .slick-slider{
    .slick-list{
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }


  .slideItem {


    padding: 14px;

    .slide-box {
      background-color: $color-white !important;
      height: 260px;
      box-shadow: 0px 2px 4px rgba(232, 232, 232, 0.5);
      border-radius: 10px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      transition: all 0.3s linear;
      cursor: pointer;

      img {
        margin: 30px auto !important;
      }

      h5 {
        text-align: center;
        margin-top: 20px;
      }
      &:hover{
        box-shadow: 0px 12px 25px rgba(2, 48, 71, 0.15);
      }
    }
  }


  .slick-dots {
    bottom: -50px;

    li {

      margin: 0;
      margin-left: 5px;

      button::before {


        background-color: #D9D9D9;
        width: 20px;
        height: 20px;
        border-radius: 50%;

        color: transparent;
        opacity: 0.7;
        font-size: 20px;
      }

    }

    .slick-active {
      button::before {

        opacity: 1;

        width: 45px;
        border-radius: 45px;
        background-color: $color-orange;
        font-size: 20px;
        z-index: 999;
      }

    }
  }

}

