.admin-layout {
  background: #F9F9F9;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;

  .admin-layout-left{
    width: 245px;
    background-color: #FFFFFF;
    border-right: 1px solid #EAEAEA;
    padding: 29px 20px;
    flex-shrink: 0;
    height: 100%;
    overflow-y: auto;

    .admin-layout-left-text{
      font-size: 24px;
      line-height: 100%;
      letter-spacing: -0.05em;
      color: #023047;
      font-weight: normal;
    }

    .nav{
      margin-top: 85px;
      .nav-item{
        .nav-link{
          padding: 16px 20px;
          border-radius: 10px;
          transition: all 0.3s linear;
          margin-bottom: 18px;
          color: #5C5C5C;
          font-size: 16px;
          line-height: 100%;
          letter-spacing: -0.03em;
          .icon{
            background-color: #333333;
            margin-right: 15px;
          }
          &.active{
            color: #FFFFFF;
            background: #FB8500;
            .icon{
              background-color: #FFFFFF;
            }
          }
        }
      }
    }
  }

  .admin-layout-right{
    width: 100%;
    height: 100%;
    overflow-y: auto;
    .admin-layout-right-navbar{
      background: #FFFFFF;
      box-shadow: 0px 2px 3px rgba(91, 96, 107, 0.09);
      height: 70px;
      padding-right: 55px;
      .icon, img{
        cursor: pointer;
        &.icon-notification{
          margin-left: 25px;
          margin-right: 35px;
        }
      }
    }

    .content{
      padding: 15px;

    }
  }
}